export default {
	data() {
		return {
			name: '我是一个名称',
			num:0,
			weihight:1200
		};
	},
	watch: {
		name(val) {
			console.log(val);
		}
	},
	computed:{
		zweihight(){
			return this.weihight>1000?(this.weihight/1000)+"kg":this.weihight+"g"
		}
	},
	methods: {
		click(e) {
			console.log(e);
			let index = e.currentTarget.dataset.index;
			console.log('我点击了');
		},
		box1event() {
			console.log('点击了外面');
		},
		box2event() {
			console.log('点击了里面');
		},
		change(){
			this.name="哈哈哈S"
		},
		add(){
			this.num++
		}
	}
};
